*{
    box-sizing: border-box;
}
table {
    
    display: table;
    border-collapse: collapse;
    color: white;
    width: 90%;
    height: 50%;
    margin: auto;
    margin-top: 60px;
}
img {
   width: 30px;
}
th {
    border-bottom: 2px solid white;
    text-align: left;
    display: table-cell;
    font-size: large;
    width: 15%;
    padding: 10px;
    color: cornflowerblue;
} 
td {
    display: table-cell;
    text-align: left;
    font-size: medium;
    width: 10%;
    margin: auto; 
    padding: 10px;
    border-bottom: 1px solid rgb(77, 76, 76);   
}
header {
    width: 100%;
    height: 40px;
    margin-bottom: 10px;
}
input {
    background-color: rgb(37, 36, 36);
    color: rgb(87, 86, 86);
    border: none;
    float: right;
}
h1 {
   color: cornflowerblue;
}
h4 {
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif ;
}
h5 {
    text-align: left;
    color: gray;
    font-family:sans-serif;
}
.newsContainer {
    display: inline-flex;
    margin: 30px;
    width: 250px;
    height: 250px;
    color: white;
}
.innerContainer {
    display: block;
    width: 100%;
    height: 100%;
}
.newsImg {
    height: 60%;
    width: 100%;
    border-radius: 5px;
}
a {
    color: white;
    text-decoration: none;
}